<template>
  <div>
    <div class='mdl-grid'>
      <div class='mdl-cell mdl-cell--3-col mdl-cell--1-col-tablet mdl-cell-hide-phone'></div>
      <div class='mdl-cell mdl-cell--6-col mdl-cel--4-col-phone'>
        <div v-for='picture in this.pictures' class='image-card' @click='displayDetails(picture.id)'>
          <div class='image-card__picture'>
            <img :src='picture.url'/>
          </div>
          <div class='image-card__comment mdl-card__actions'>
            <span>{{picture.comment}}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style scoped>
.add-picture-button {position: fixed; right: 24px; bottom: 24px; z-index: 998}
.image-card {position: relative; margin-bottom: 8px}
.image-card__picture > img {width:100%}
.image-card__comment {position: absolute; bottom: 0; height: 52px; padding: 16px; text-align: right; background: rgba(0, 0, 0, 0.5)}
.image-card__comment > span {color: #fff; font-size: 14px; font-weight: bold}
</style>
<script>
import data from '../data'
export default ({
  data() {return {pictures:data.pictures
  }},
  methods: { displayDetails(id) {this.$router.push({name:'detail', params:{id:id}})}
  }
})
</script>



