<template>
  <div id="app">
    <div class='mdl-layout mdl-js-layout mdl-layout--fixed-header'>
      <header class='mdl-layout__header'>
        <div class='mdl-layout__header-row'>
          <span class='mdl-layout-title'>CropChat</span>
        </div>
      </header>
      <div class='mdl-layout__drawer'>
        <span class='mdl-layout-title'>CropChat</span>
        <nav class='mdl-navigation'>
          <a class='mdl-navigation__link' href='/#/' @click='hideMenu'>Home</a>
          <a class='mdl-navigation__link' href='/#/post' @click='hideMenu'>Post a picture</a>
        </nav>
      </div>
      <main class='mdl-layout__content'>
        <div class='page-content'>
          <router-view></router-view>
        </div>
      </main>
    </div>
  </div>
</template>

<script>
require('material-design-lite')
export default {
  name: 'app',
  methods: function() {
    document.getElementByClassName('mdl-layout__drawer')[0].classList.remove('is-visible')
    document.getElementByClassName('mdl-layout__obfuscator')[0].classList.remove('is-visible')
  }
}
</script>

<style>
@import url('https://fonts.googleapis.com/icon?family=Material+Icons');
@import url('https://code.getmdl.io/1.2.1/material.blue-red.min.css');

body {
  margin: 0;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

main {
  text-align: center;
  margin-top: 40px;
}

header {
  margin: 0;
  height: 56px;
  padding: 0 16px 0 24px;
  background-color: #4fc08d;
  color: #ffffff;
}

header span {
  display: block;
  position: relative;
  font-size: 20px;
  line-height: 1;
  letter-spacing: .02em;
  font-weight: 400;
  box-sizing: border-box;
  padding-top: 16px;
}
</style>



